        .singleTop-txt-deco
          p Segn Carballar (2013), internet es una herramienta de comunicacin formada por una red de ordenadores de diferentes tipos, interconectados a travs de diversas tecnologas. Pero lo que hace que todos puedan funcionar juntos es la existencia de un estndar en el proceso de comunicacin llamado TCP/IP o Protocolo de Control de Transmisin/ Protocolo de Internet, el cual hace posible la transferencia de datos en la red y entre equipos informticos. Un estudio completo de TCP/IP debe llevar a la configuracin de una red especfica para la ptima transmisin de la informacin, si algn da se quiere profundizar en la arquitectura de este modelo es importante leer el artculo: #[i Anlisis de una Red en un Entorno Ipv6: Una Mirada Desde Las Intrusiones de Red y El Modelo TCP/IP] que se encuentra en la bibliografa.
      .col-lg-5
        figure
          img(src="@/assets/curso/tema3/img_3_01.svg", alt="alt", style="width:550px;").m-auto

    p.mb-5 Es necesario recordar lo que esta herramienta representa en la figura 1, a continuacin:

    .row.justify-content-center.mb-5
      .col-lg-8
        .titulo-sexto.color-acento-contenido
