            h5 Correo electrnico
            p Servicio que permite enviar mensajes de un ordenador -PC- a otro por medio de protocolos o sistemas de transmisin que integra formatos de texto, imagen y audio entre otros, adems de adjuntar archivos. Para enviar un correo o e-mail solo se necesita tener acceso a la red a travs de un proveedor de servicio de internet -ISP- y crear una cuenta de correo con nombre de usuario y contrasea.        
          .col-md-6
            figure
              img(src='@/assets/curso/tema3/Slide_01.jpg', alt='Texto que describa la imagen')
        .row
          .col-md-6.mb-4.mb-md-0
            h5 #[i Streaming]
            p Es el servicio que permite mostrar un contenido multimedia en la red de internet, como por ejemplo la reproduccin de un video o un audio. Ya que estos archivos consumen mucha memoria en los servidores y PC y para que su transmisin sea rpida se necesita un protocolo o sistema llamado RTP (#[i Real-Time Transport Protocol]) que significa protocolo de transmisin en tiempo real, el cual provee velocidad y fiabilidad en cuanto a que toda la informacin se podr visualizar y escuchar.       
          .col-md-6
            figure
              img(src='@/assets/curso/tema3/Slide_02.jpg', alt='Texto que describa la imagen')
        .row
          .col-md-6.mb-4.mb-md-0
            h5 Llamadas y video conferencias
            p Hoy se pueden hacer llamadas utilizando la red de internet y es lo que se llama telefona IP (Protocolo de Internet), igualmente realizar video conferencias en diferentes entornos a travs de protocolos de transmisin (sistemas) y aplicaciones como Skype, Teams, Meet, entre otras, las cuales permiten la comunicacin visual y auditiva.      
          .col-md-6
            figure
              img(src='@/assets/curso/tema3/Slide_03.jpg', alt='Texto que describa la imagen')
        .row
          .col-md-6.mb-4.mb-md-0
            h5 Transferencia de archivos
            p Para realizar la transmisin de archivos entre PC (computadoras personales) es necesario contar con un protocolo o sistema de transferencia que permita la comunicacin entre los dispositivos a travs de la red. El protocolo mas utilizado es el FTP (#[i File Transfer Protocol]) o protocolo de transferencia de ficheros, el cual permite que la informacin viaje en paquetes de bits o unidades de memoria pequeas en la red de internet.      
          .col-md-6
            figure
              img(src='@/assets/curso/tema3/Slide_04.jpg', alt='Texto que describa la imagen')
        .row
          .col-md-6.mb-4.mb-md-0
            h5 Mensajera instantnea
            p  Es la forma de comunicacin que se utiliza en tiempo real utilizando aplicaciones como el WhatsApp o Facebook que permiten el envo instantneo de mensajes en una red como internet o utilizando red mvil como 3G, 4G, etc.       
          .col-md-6
            figure
              img(src='@/assets/curso/tema3/Slide_05.jpg', alt='Texto que describa la imagen')
        .row
          .col-md-6.mb-4.mb-md-0
            h5 #[i Web]
            p  Este servicio nos permite realizar diferentes actividades de manera remota; como consultar textos, imgenes, videos, audios y enviar informacin entre muchas otras. La informacin a la que pueden acceder los usuarios est disponible a travs de sitios (conjunto de pginas #[i web]) o pginas #[i web] a los que se puede acceder a travs de los navegadores #[i web].         
          .col-md-6
            figure
