    #t_3_1.titulo-segundo
      h2 3.1 Navegador #[i web]

    p.mb-5 Para acceder a internet los usuarios necesitan normalmente un ordenador o PC, una red (telefnica, de TV, mvil, radio, satlite, entre otros.), un dispositivo de comunicacin (mdem) y un proveedor de servicio. Adems de lo anterior para acceder a un sitio en la #[i web] es indispensable poseer un navegador que lo permita. 
         
    .bloque-texto-a.custom-clr1.p-4.p-md-5.mb-5 
