            p Un programa que permite ver la informacin que contiene el sitio #[i web] visitado.

    .row.justify-content-center
      .col-lg-6.order-2.order-lg-1
        p.mb-5 Segn Lpez Sanz (2016), los navegadores suelen diferenciarse por una serie de criterios como:

        AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta tarjeta--azul")
          .row(titulo="Plataforma de ejecucin")
            .col-12
              p Se puede llamar plataforma de ejecucin al sistema operativo del ordenador, el cual facilita la interaccin entre el usuario, el #[i hardware] y el #[i software], respecto a esto el navegador safari de los PC marca Apple solo funcionan en el sistema operativos IOS.
          div(titulo="Caractersticas del navegador")
            .col-12
              p Los navegadores ofrecen funcionalidades como administracin de marcadores, almacenamiento seguro de contraseas digitadas en el PC y datos digitados en formulario, correccin ortogrfica y herramientas de bsqueda.
          div(titulo="Funcionalidades de accesibilidad")
            .col-12
              p Estas funciones corresponden a la personalizacin de la interfaz que visualiza el usuario; por ejemplo, permitir que pueda navegar por pestaas, bloquear las ventanas emergentes, habilitar opciones de zoom (alejar o acercar) y funciones de bsqueda de textos entre otros.
          div(titulo="Licencia de #[i software]")
            .col-12
              p Existen los navegadores de cdigo libre para modificaciones si desea el usuario y cdigo propietario que no permite el acceso para la realizacin de cambios, como se ver a continuacin en los tipos de navegadores.

