    p.mb-5 De acuerdo con la estructura anterior la accin que debe realizar un usuario para acceder a un sitio #[i web] es simplemente introducir su direccin (URL) en la barra de direcciones para que el navegador muestre el contenido en el rea de la pgina. 

    .container__m_4.bw3
      .row.justify-content-center
        .col-lg-7
          h4 Servidor #[i web]
          p Un servidor es un computador o tambin se suele llama ordenador conectado a internet que almacena cualquier tipo de informacin y est diseado para responder a las peticiones de los usuarios. 
          p.mb-4 A diferencia de un servidor cualquiera, un servidor #[i web] es un computador que est asociado al #[i hardware] y al #[i software] trabajando en combinacin para: 

          ul.fa-ul.lista-ul--naraja.mb-5
            li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] En cuanto al #[i hardware]: almacenar todos los archivos que hacen parte de un sitio #[i web]; por ejemplo, los documentos escritos en lenguaje de cdigo HTML, las hojas de estilo CSS y los archivos JavaScript. Responde a las peticiones que realiza el usuario respecto a los sitios #[i web] cuando introduce una direccin URL (#[i Uniform Resource Locator] o localizador Uniforme de Recursos) representada por un nombre: sena.edu. 
            li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] En cuanto al #[i software]: poder entregar los archivos solicitados por el usuario haciendo uso del protocolo HTTP o protocolo de transferencia de hipertexto, que es el que utiliza el navegador para que se puedan visualizar las pginas #[i web].

        .col-lg-5
          img(src="@/assets/curso/tema3/img_3_10.png", alt="alt", style="width:350px;").m-auto  

    p.mb-5 De acuerdo con lo anterior cuando el usuario hace una peticin de un sitio #[i web] a travs del navegador, este lo solicita al servidor #[i web] mediante el protocolo HTTP. Cuando la peticin llega al servidor correspondiente (#[i hardware]), el HTTP (#[i software]) recibe el pedido y enva el sitio #[i web] solicitado tambin por la va HTTP.           

    h4.mb-5 Tipos de servidores

    .row.justify-content-center.mb-5
      .col-lg-5.mb-4.mb-lg-0
        .tarjeta.tarjeta-mint
          figure.mb-4
            img(src="@/assets/curso/tema3/img_3_11.svg", alt="alt", style="width:350px;").m-auto
          h5.text-center Servidor #[i web] esttico
          p.mb-0 Es un computador que aloja sitios #[i web] y contiene #[i software] HTTP que enva los archivos tal como estn almacenados, cuando son solicitados a travs de un navegador. Lo anterior quiere decir que el contenido del sitio #[i web] tambin es esttico, ya que se conserva invariante ante las acciones que pueda realizar el usuario y su objetivo es presentar datos que no cambian con el paso del tiempo.
      .col-lg-5
        .tarjeta.tarjeta-mint
          figure.mb-4
            img(src="@/assets/curso/tema3/img_3_12.svg", alt="alt", style="width:350px;").m-auto
          h5.text-center Servidor #[i web] dinmico
          p.mb-0 Es un computador que aloja sitios #[i web] y contiene #[i software] HTTP y bases de datos que actualizan los archivos almacenados; por lo tanto, interacta con el usuario y responde a sus solicitudes procesando la informacin entregada por ste. Lo anterior significa que el contenido de los sitios #[i web] solicitados cambia con el tiempo, sufriendo alteraciones y modificaciones por las acciones que realiza el usuario sobre los datos que presenta.

    p Como se observa en la imagen anterior los servidores #[i web] estticos entregan la informacin tal cual como la tiene almacenada al ser requerida por el cliente sin hacer ninguna modificacin y los servidores #[i web] dinmicos procesan la informacin solicitada a travs de una base de datos antes de ser entregada al cliente. 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.mb-4.mb-lg-0        
        figure.mb-4
          img(src="@/assets/curso/tema3/img_3_13.png", alt="alt", style="width:350px;").m-auto
      .col-lg-7
        h5 Cundo utilizar un servidor esttico?
        p Se utiliza cuando se solicitan sitios o pginas #[i web] meramente informativas y si eres t el desarrollador se deber utilizar este servidor cuando se necesita construir pginas que contienen texto, imgenes o fotos que ilustraran al usuario sobre algn tema o producto que quieres que ellos conozcan y solamente se informen.     

        h5 Cundo utilizar un servidor dinmico? 
        p Se utiliza cuando se requiera procesar algn tipo de informacin al interactuar con el sitio o pgina #[i web]. Entonces se puede utilizar este servidor cuando se requiera, por ejemplo:

        ul.fa-ul.lista-ul--naraja.mb-5
          li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Crear una tienda virtual de venta de productos o servicios o comercio electrnico (e-commerce). 
          li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Crear un blog con el objeto de publicar contenidos de un tema especfico de inters para un pblico objetivo que puede dejar sus comentarios. 
          li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Crear foros para propiciar espacios de discusin sobre temas de inters.
          li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Crear un portal #[i web] con diferentes recursos como enlaces a otros sitios o buscadores.
          li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Crear pginas sencillas con pocos enlaces e informacin bsica con el objeto de conseguir que los visitantes sean seguidores diligenciando formularios de inscripcin a la pgina.

    h3 #[i Hosting]

    p El #[i hosting] es el espacio de memoria proporcionado por los servidores #[i web] para el alojamiento de los archivos de la aplicacin #[i web] que se tenga, compuesto por documentos con lenguaje de cdigo HTML, PHP, Java Scripts, hojas de estilo CSS, imgenes, documentos PDF, entre otros. Con el servicio de alojamiento los sitios o pginas #[i web] estarn disponibles en la red de internet para que puedan ser consultados por los usuarios o clientes en cualquier parte del mundo y a cualquier hora.

    p.mb-5 El #[i hosting] lo proveen las empresas prestadoras de servicios de internet que poseen los servidores en propiedad o en alquiler con las especificaciones de #[i hardware] y #[i software] para el almacenamiento de las pginas #[i web] de los clientes.  Estas empresas ofrecen el #[i hosting] a travs de planes con pagos anuales y con una serie de servicios incluidos de acuerdo con el tipo de plan contemplando las siguientes caractersticas:

    .container__m_4.bw1.mb-5
      .row.justify-content-center
        .col-lg-7
          ul.fa-ul.lista-ul--naraja
            li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Cantidad de espacio o memoria disponible para alojar los archivos que conforman el sitio #[i web].
            li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Transferencia mxima de archivos mensualmente generados por los visitantes y enviaos al servidor.
            li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Nmero de cuentas de correo asociadas al dominio (empresa) de acuerdo con el plan contratado.
            li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Registrar un dominio de primer nivel, por ejemplo: (.com), (.org), (.edu), etc, los cuales se vern en la seccin de Dominios.
            li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Contratar un nmero determinado de bases de datos para los contenidos #[i web] dinmicos, es decir que tienen interaccin con el usuario.

        .col-lg-5.mb-4.mb-lg-0        
          figure
            img(src="@/assets/curso/tema3/img_3_14.png", alt="alt", style="width:350px;").m-auto

    .hr_blank

    h3 Tipos de #[i hosting]   

    p De acuerdo con los requisitos tcnicos de cada proyecto en cuanto a necesidades de programacin, bases de datos, transferencia mensual o espacio de memoria, el usuario puede necesitar un tipo de alojamiento especfico. Por ejemplo: 

    TabsC.color-acento-botones.mb-5
      .py-3.py-md-4(titulo="Hosting compartido")

        .row.justify-content-center.align-items-center
          .col-md-5.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema3/img_3_15.svg', alt='marketing', style="width:300px;").m-auto              

          .col-md-6.mb-4.mb-md-0            
            p El servidor proporciona los mismos recursos de potencia de procesamiento, velocidad, memoria RAM (Memoria de Acceso Aleatorio o memoria de almacenamiento temporal que contiene lo que est trabajando el usuario en el momento) y dems, que posea para todas las cuentas de usuario que tengan este servicio de alojamiento. 
            p Es decir, cada cliente que tiene su espacio de almacenamiento en este servidor comparte los recursos de memoria, CPU y todos los aspectos de rendimiento de la mquina, tiene restricciones de instalacin de programas en el servidor. 

      .py-3.py-md-4(titulo="Hosting VPS")
        .row.justify-content-center.align-items-center
          .col-md-5.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema3/img_3_15.svg', alt='marketing', style="width:350px;").m-auto               

          .col-md-6.mb-4.mb-md-0            
            p Virtual Private Service o servidor virtual privado, en el cual los clientes comparten un mismo servidor fsico que se ha dividido virtualmente en compartimentos de memoria con asignacin de recursos propios de RAM, CPU, espacio de almacenamiento, transferencia de datos y con la posibilidad de personalizarlo con el sistema operativo de mayor conveniencia.

      .py-3.py-md-4(titulo="Hosting Dedicado")
        .row.justify-content-center.align-items-center
          .col-md-5.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema3/img_3_15.svg', alt='marketing', style="width:350px;").m-auto                

          .col-md-6.mb-4.mb-md-0            
            p Ofrece un servidor fsico completo para el proyecto del cliente, no comparte recursos y tiene acceso a los dispositivos de #[i hardware] y las aplicaciones de #[i software], sin ningn tipo de restriccin para instalaciones. 
            p El cliente puede tener el control total de sus aplicaciones y la administracin del servidor. 
            
      .py-3.py-md-4(titulo="Hosting en la nube (cloud)")
        .row.justify-content-center.align-items-center
          .col-md-5.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema3/img_3_15.svg', alt='marketing', style="width:350px;").m-auto                

          .col-md-6.mb-4.mb-md-0            
            p La pgina #[i web] del cliente no est alojada en un solo servidor sino en varios servidores interconectados en la nube, con el objeto de que, si un servidor falla ante la solicitud de un usuario, el otro disponible puede entrar a solventar el problema. 

    separador
    #t_3_3.titulo-segundo
      h2 3.3 Dominios

    .container__m_4.bw1.p-4
      .row
        .col-lg-5
          figure
            img(src="@/assets/curso/tema3/img_3_15.png", alt="alt", style="width:350px").m-auto
        .col-lg-7
          p Los sitios o pginas #[i web] se alojan en servidores que tienen una direccin IP (protocolo/internet) la cual identifican los navegadores cuando solicitan la pgina #[i web]. Esta direccin es un conjunto de cuatro grupos de nmeros, por ejemplo:
          p #[strong.orange IP: 210.23.87.122]
          p Debido a la dificultad para los usuarios el tener que recordar estos nmeros, las organizaciones que regulan el funcionamiento de los servidores a nivel mundial se unieron para crear la organizacin ICANN (#[i Internet Corporation for Asigned Names and Numbers]) con el objetivo de establecer un sistema para gestionar los dominios utilizando una combinacin alfanumrica (letras, nmeros y smbolos) los cuales identifican un servidor y una pgina determinada para facilitar la navegacin por internet. Dicho sistema se denomina DNS (#[i Domain Name System]) o Sistema de Nombres de Dominio, los cuales son almacenados en Servidores llamados por el mismo nombre: Servidores de Nombres de Dominio  DNS-.
             

    .bg-mint.pt-4
      .row.mb-5
        p.mb-5 A continuacin, se dar a conocer cules son los tipos de Dominio: 
        h4 Concepto de dominio
        .col-lg-7
          p El dominio es el nombre con el que un sitio o pgina #[i web] se identifica en la red de internet, el cual es solicitado al DNS mediante un navegador #[i web] que permite acceder al contenido requerido por el usuario. 
            br
            br
            | Los nombres de dominio se componen de dos partes: la etiqueta y la extensin. La etiqueta sirve para determinar el nombre de una entidad, empresa, asociacin, persona, etc. y la extensin indica el tipo o categora de dominio dependiendo de la razn social o actividad a la que se dedica, como se ver a continuacin. 
            br
            br
            | Los dominios son indexados por los motores de bsqueda, por ello es aconsejable colocar un nombre que posiblemente el usuario pueda relacionar con su necesidad.
        .col-lg-5
          figure
            img(src="@/assets/curso/tema3/img_3_16.png", alt="alt", style="width:350px").m-auto 
      
      h4 Formacin de un Dominio
      p.mb-4 Para formar un nombre de dominio se deben tener en cuenta las siguientes reglas de sintaxis:
      ul.fa-ul.lista-ul--naraja.mb-4
        li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] No utilizar espacios en el nombre.
        li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] No utilizar caracteres con acento o especiales (tilde, ?, *, , $, %, etc.).
        li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Utilizar las letras del alfabeto espaol (A-Z), incluyendo la .
        li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Se pueden utilizar los nmeros (0-9).
        li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Colocar solo letras minsculas puesto que no se tienen en cuenta las maysculas.
        li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] No utilizar nombres de ciudades.
        li.mb-3 #[span.fa-li #[i.fas.fa-pen-square]] Puede contemplarse entre 3 y 63 caracteres.
      p Cuando se tiene seleccionado el nombre se debe proceder al registro de este, a travs de un proveedor autorizado ante la ICANN que verifica si ya existe o si se puede utilizar. Realizada esta comprobacin el usuario puede adquirirlo.    

    h3.mt-5 Tipos de dominio
    p.mb-5 Existen cuatro tipos de dominio clasificados jerrquicamente de acuerdo con las reglas establecidas por el Sistema de Nombres de Dominio -DNS-: 

